module.exports = "<template><u-article><h1>UTimePicker 时间选择</h1>
<h2>示例</h2>
<h3>基本形式</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-time-picker.vue/README.md">
    <div><u-time-picker time="15:46:33"></u-time-picker></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-time-picker</span> <span class="hljs-attr">time</span>=<span class="hljs-string">"15:46:33"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-time-picker</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>只读和禁用</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-time-picker.vue/README.md">
    <div><u-linear-layout>
    <u-time-picker time="15:46:33" readonly="readonly"></u-time-picker>
    <u-time-picker time="15:46:33" disabled="disabled"></u-time-picker>
</u-linear-layout></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-time-picker</span> <span class="hljs-attr">time</span>=<span class="hljs-string">"15:46:33"</span> <span class="hljs-attr">readonly</span>=<span class="hljs-string">"readonly"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-time-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-time-picker</span> <span class="hljs-attr">time</span>=<span class="hljs-string">"15:46:33"</span> <span class="hljs-attr">disabled</span>=<span class="hljs-string">"disabled"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>日期范围</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-time-picker.vue/README.md">
    <div><u-time-picker time="15:46:33" min-time="12:30:00" max-time="14:45:00"></u-time-picker></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-time-picker</span> <span class="hljs-attr">time</span>=<span class="hljs-string">"15:46:33"</span> <span class="hljs-attr">min-time</span>=<span class="hljs-string">"12:30:00"</span> <span class="hljs-attr">max-time</span>=<span class="hljs-string">"14:45:00"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-time-picker</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>日期最小单位</h3>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-time-picker.vue/README.md">
    <div><anondemo-7d6ceeea-357 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-time-picker</span> <span class="hljs-attr">minUnit</span>=<span class="hljs-string">"minute"</span> <span class="hljs-attr">min-time</span>=<span class="hljs-string">"12:30"</span> <span class="hljs-attr">max-time</span>=<span class="hljs-string">"14:45"</span> <span class="hljs-attr">:time</span>=<span class="hljs-string">"time"</span> @<span class="hljs-attr">change</span>=<span class="hljs-string">"change($event.time)"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
	data() {
		<span class="hljs-keyword">return</span> {
			<span class="hljs-attr">time</span>: <span class="hljs-string">'13:12'</span>,
		};
	},
    <span class="hljs-attr">methods</span>: {
        change(time) {
            <span class="hljs-keyword">this</span>.time = time;
            <span class="hljs-built_in">console</span>.log(time);
        },
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>方法</h3>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-time-picker.vue/README.md">
    <div><anondemo-18ba7189-306 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-time-picker</span> <span class="hljs-attr">:time</span>=<span class="hljs-string">"time"</span> @<span class="hljs-attr">change</span>=<span class="hljs-string">"change($event.time)"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
	data() {
		<span class="hljs-keyword">return</span> {
			<span class="hljs-attr">time</span>: <span class="hljs-string">'20:12:12'</span>,
		};
	},
    <span class="hljs-attr">methods</span>: {
        change(time) {
            <span class="hljs-keyword">this</span>.time = time;
            <span class="hljs-built_in">console</span>.log(time);
        },
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h2>TimePicker API</h2>
<h3>Props/Attrs</h3>
<table>
<thead>
<tr>
<th>Prop/Attr</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>time</td>
<td>String</td>
<td>'00:00'</td>
<td>当前的时间值</td>
</tr>
<tr>
<td>minUnit</td>
<td>String</td>
<td>'second'</td>
<td>最小时间单位<code>'second'</code>或者是<code>'minute'</code></td>
</tr>
<tr>
<td>minTime</td>
<td>String</td>
<td>'00:00'</td>
<td>最小时间</td>
</tr>
<tr>
<td>maxTime</td>
<td>String</td>
<td>'23:59'</td>
<td>最大时间</td>
</tr>
<tr>
<td>autofocus</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否自动获得焦点</td>
</tr>
<tr>
<td>readonly</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否只读</td>
</tr>
<tr>
<td>disabled</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否禁用</td>
</tr>
<tr>
<td>visible</td>
<td>Boolean</td>
<td><code>true</code></td>
<td>是否显示</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<h4>@change</h4>
<p>时间改变时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>time</td>
<td>Object</td>
<td>改变后的时间</td>
</tr>
</tbody>
</table>
</u-article></template>

<script>
    import Component0 from "./anondemo-7d6ceeea-357.vue";
import Component1 from "./anondemo-18ba7189-306.vue";

    export default {
        components: {
'anondemo-7d6ceeea-357': Component0,
'anondemo-18ba7189-306': Component1,
},
    }
</script>
            "


