</u-linear-layout></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-date-time-picker</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-time-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-date-time-picker</span> <span class="hljs-attr">date</span>=<span class="hljs-string">"2018-08-08"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>自动对焦</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-date-time-picker.vue/README.md">
    <div><u-date-time-picker date="2018-08-08" autofocus></u-date-time-picker></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-date-time-picker</span> <span class="hljs-attr">date</span>=<span class="hljs-string">"2018-08-08"</span> <span class="hljs-attr">autofocus</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-time-picker</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>只读和禁用</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-date-time-picker.vue/README.md">
    <div><u-linear-layout>
    <u-date-time-picker date="2018-08-08" readonly></u-date-time-picker>
    <u-date-time-picker date="2018-08-08" disabled></u-date-time-picker>
</u-linear-layout></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-date-time-picker</span> <span class="hljs-attr">date</span>=<span class="hljs-string">"2018-08-08"</span> <span class="hljs-attr">readonly</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-time-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-date-time-picker</span> <span class="hljs-attr">date</span>=<span class="hljs-string">"2018-08-08"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-time-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>最大值和最小值</h3>
<u-code-example
    :show-code="true"
