    file-path="src/components/u-date-time-picker.vue/README.md">
    <div><u-date-time-picker date="2017-05-12 8:00:00" min-date="2017-01-10 07:00:00" max-date="2017-12-12 07:00:00"></u-date-time-picker></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-date-time-picker</span> <span class="hljs-attr">date</span>=<span class="hljs-string">"2017-05-12 8:00:00"</span> <span class="hljs-attr">min-date</span>=<span class="hljs-string">"2017-01-10 07:00:00"</span> <span class="hljs-attr">max-date</span>=<span class="hljs-string">"2017-12-12 07:00:00"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-time-picker</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>方法</h3>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-date-time-picker.vue/README.md">
