		<span class="hljs-keyword">return</span> {
			<span class="hljs-attr">date</span>: <span class="hljs-string">'2017-12-01 20:12:12'</span>,
		};
	},
    <span class="hljs-attr">methods</span>: {
        change(time) {
            <span class="hljs-built_in">console</span>.log(time);
        },
        select(date) {
            <span class="hljs-built_in">console</span>.log(date);
        }
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h2>DateTimePicker API</h2>
<h3>Props/Attrs</h3>
<table>
<thead>
<tr>
<th>Prop/Attr</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>date.sync</td>
<td>String, Number, Date</td>
<td></td>
<td>显示的日期值</td>
</tr>
<tr>
<td>minDate</td>
<td>String, Number, Date</td>
<td></td>
<td>最小日期值</td>
</tr>
<tr>
<td>maxDate</td>
<td>String, Number, Date</td>
<td></td>
<td>最大日期值</td>
</tr>
<tr>
<td>placeholder</td>
<td>String</td>
<td><code>请选择时间</code></td>
<td>默认提示语</td>
</tr>
<tr>
<td>autofocus</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否默认处于focus状态</td>
</tr>
<tr>
<td>disabled</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否禁用</td>
</tr>
<tr>
<td>readonly</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否只读</td>
</tr>
<tr>
<td>yearDiff</td>
<td>String, Number</td>
<td><code>3</code></td>
<td>最小可选年份值为当前年减去此值</td>
</tr>
<tr>
<td>yearAdd</td>
<td>String, Number</td>
<td><code>1</code></td>
<td>最大可选年份值为当前年加上此值</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<h4>@select</h4>
<p>日期值发生变化触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.date</td>
<td>Number</td>
<td>选择项的值，默认返回时间戳</td>
</tr>
</tbody>
</table>
<h4>@change</h4>
<p>选择新时间触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.date</td>
<td>Number</td>
<td>选择项的值，默认返回时间戳</td>
</tr>
</tbody>
</table>
</u-article></template>

<script>
    import Component0 from "./anondemo-4a6f154d-386.vue";

    export default {
        components: {
'anondemo-4a6f154d-386': Component0,
