    <span class="hljs-tag">&lt;<span class="hljs-name">u-date-picker</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-date-picker</span> <span class="hljs-attr">date</span>=<span class="hljs-string">"2018-08-08"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
</u-code-example>

