    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-date-picker</span> <span class="hljs-attr">date</span>=<span class="hljs-string">"2018-08-08"</span> <span class="hljs-attr">autofocus</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-picker</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>弹窗对其方式</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-date-picker.vue/README.md">
    <div><u-linear-layout>
    <u-date-picker placeholder="请选择日期" alignment="left"></u-date-picker>
    <u-date-picker placeholder="请选择日期" alignment="right"></u-date-picker>
</u-linear-layout></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-date-picker</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请选择日期"</span> <span class="hljs-attr">alignment</span>=<span class="hljs-string">"left"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-date-picker</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请选择日期"</span> <span class="hljs-attr">alignment</span>=<span class="hljs-string">"right"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>最大值和最小值</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-date-picker.vue/README.md">
    <div><u-date-picker date="2018-08-08" min-date="2018-08-06" max-date="2018-08-18"></u-date-picker></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-date-picker</span> <span class="hljs-attr">date</span>=<span class="hljs-string">"2018-08-08"</span> <span class="hljs-attr">min-date</span>=<span class="hljs-string">"2018-08-06"</span> <span class="hljs-attr">max-date</span>=<span class="hljs-string">"2018-08-18"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-picker</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>只读和禁用</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-date-picker.vue/README.md">
