    <u-date-picker readonly date="2018-08-08"></u-date-picker>
    <u-date-picker disabled date="2018-08-08"></u-date-picker>
</u-linear-layout></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-date-picker</span> <span class="hljs-attr">readonly</span> <span class="hljs-attr">date</span>=<span class="hljs-string">"2018-08-08"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-date-picker</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">date</span>=<span class="hljs-string">"2018-08-08"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-date-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
