<h3>方法</h3>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-date-picker.vue/README.md">
    <div><anondemo-f5c0a8d2-586 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-date-picker</span> <span class="hljs-attr">time</span>=<span class="hljs-string">"morning"</span> <span class="hljs-attr">year-diff</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">year-add</span>=<span class="hljs-string">"5"</span> <span class="hljs-attr">:date.sync</span>=<span class="hljs-string">"date"</span> @<span class="hljs-attr">change</span>=<span class="hljs-string">"change"</span> @<span class="hljs-attr">select</span>=<span class="hljs-string">"select"</span> @<span class="hljs-attr">toggle</span>=<span class="hljs-string">"toggle($event)"</span>&gt;</span>Modal<span class="hljs-tag">&lt;/<span class="hljs-name">u-date-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
