		}
	},
    <span class="hljs-attr">methods</span>: {
        change(newVal) {
            <span class="hljs-built_in">console</span>.log(newVal);
        },
        select(newVal) {
        	<span class="hljs-built_in">console</span>.log(newVal);
        },
        toggle(event) {
        	<span class="hljs-built_in">console</span>.log(event.open);
        }
    },
    <span class="hljs-attr">watch</span>: {
        date(newValue) {
            <span class="hljs-built_in">console</span>.log(newValue);
        },
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h2>DatePicker API</h2>
<h3>Attrs/Props</h3>
<table>
<thead>
<tr>
<th>Attr/Prop</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>width</td>
<td>String, Number</td>
<td><code>160</code></td>
<td>输入框宽度</td>
</tr>
<tr>
<td>date.sync</td>
<td>String,Number,Date</td>
<td></td>
<td>初始日期值</td>
</tr>
<tr>
<td>minDate</td>
<td>String,Number,Date</td>
<td></td>
<td>最小日期</td>
</tr>
<tr>
<td>maxDate</td>
<td>String,Number,Date</td>
<td></td>
<td>最大日期</td>
</tr>
<tr>
<td>readonly</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否只读</td>
</tr>
<tr>
<td>disabled</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否禁用</td>
</tr>
<tr>
<td>autofocus</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>文本框默认处于focus</td>
</tr>
<tr>
<td>placeholder</td>
<td>String</td>
<td><code>请输入</code></td>
<td>文本框默认提示</td>
</tr>
<tr>
<td>alignment</td>
<td>String</td>
<td><code>left</code></td>
<td>日历弹窗对其方式，可选值：<code>left</code>, <code>right</code></td>
</tr>
<tr>
<td>time</td>
<td>String, Number</td>
<td><code>start</code></td>
<td>设置返回的日期的时分秒值，可选值为<code>start</code>,表示时分秒是0:00:00,<code>morning</code>:时分秒为8:00:00,<code>end</code>:表示时分秒为: 23:59:59, 输入整数表示对应的整数时刻，例如输入9代表时分秒为9:00:00,自定义时分秒请输入对应格式的字符串即可</td>
</tr>
<tr>
<td>yeadDiff</td>
<td>String, Number</td>
<td><code>3</code></td>
<td>当前年份前几年</td>
</tr>
<tr>
<td>yearAdd</td>
<td>String, Number</td>
<td><code>1</code></td>
<td>当前年份后几年</td>
</tr>
</tbody>
</table>
